<template>
  <Layout>
    <div class="login">
      <div class="login-form">
        <h1>手机号登录</h1>
        <input class="phoneNumber"
               type="tel" placeholder="请输入手机号"
               maxlength="11"
               v-model="phoneNumber">
        <span class="note">未注册的手机号通关验证将自动注册</span>
        <button :disabled="check"
                @click="submit">
          发送短信验证码
        </button>
        <span class="passwordLogin">已有账号，
          <router-link to="/passwordToLogin">密码登录</router-link>
        </span>
        <div class="agree-protocol"
             @click="agree">
          <Icon v-if="agreeProtocol" name="success"/>
          <span class="circle" v-else></span>
          <p>已阅读并同意<span>《实名认证服务协议》《隐私权政策》</span></p>
        </div>
      </div>
    </div>
  </Layout>
</template>

<script>
export default {
  name: 'LoginView',
  data() {
    return {
      phoneNumber: '',
      agreeProtocol: false
    }
  },
  computed: {
    check() {
      if (!this.agreeProtocol ||
          this.phoneNumber.length !== 11) {
        return true
      } else {
        return !(/^1[3456789]\d{9}$/.test(this.phoneNumber))
      }
    }
  },
  methods: {
    agree() {
      this.agreeProtocol = !this.agreeProtocol
    },
    submit() {
      console.log(1)
      console.log(this.phoneNumber)
      // 跳转到接受验证码页面
      this.$router.push({
        path: '/receiveCode',
      })
    }
  }
}
</script>

